.wallet-menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    
    border-radius: 5px;
    overflow: hidden;
    padding: 0px 10px;
}

.wallet-connect-btn {
    border: 2px solid #000;
    padding: 4px;
    color: #000;
    width: 115px;
    height: 35px;
    background-color: #fff;
    font-size: 15px;
    cursor: pointer;
    border-radius: 3px;
    overflow: hidden;
    text-overflow: ellipsis;
}
